@import "styles/variables.scss";

.shopBlock {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
    width: 100%;
    padding: 24px;
    border: 1px solid rgba(35, 35, 35, 0.20);
    margin-bottom: 16px;
}

.shopBlockData {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.shopBlockData > div > p:first-of-type {
    font-size: 28px;
    font-weight: 450;
    line-height: 120%;
    letter-spacing: -0.56px;
    margin-bottom: 20px;
}

.shopBlockData > div > p:not(:first-of-type) {
    font-size: 16px;
    font-weight: 400;
    line-height: 125%;
    letter-spacing: -0.32px;
}

.shopBlockData > div > p:nth-of-type(2) {
    margin-bottom: 24px;
}

.shopBlockData > div > p:last-child {
    margin-top: 8px;
}

.shopBlock > img {
    width: 100%;
    height: auto;
}

.partners > div {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 24px 0;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

.partners > div:last-child {
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
}

.doubleNumber > a {
    text-decoration: none;
    font-size: 16px;
}

.partners > div > div:first-child > p:first-child {
    font-size: 28px;
    margin-bottom: 24px;
}

.partners > div > div:first-child > :nth-child(2) {
    display: block;
    margin-bottom: 8px;
}

.partners > div > div:first-child > a {
    text-decoration: none;
}

.partners > div > div:first-child > p:not(:first-child),
.partners > div > div:first-child > a {
    text-transform: uppercase;
}

.partners > div > div:last-child {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.partners > div > div:last-child > p:first-child {
    margin-bottom: 46px;
    text-transform: uppercase;
}

@media screen and (max-width: 1680px) {
    .content {
        margin-left: 140px;
        margin-right: 140px;
    }
}

@media #{$tablet} {
    .content {
        margin-left: 20px;
        margin-right: 20px;
    }
    .shopBlock {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr auto;
        gap: 16px;
        padding: 16px;
        margin-bottom: 24px;
        width: auto;
    }
    .shopBlockData {
        gap: 48px;
    }
    .shopBlockData > div > p:first-of-type {
        font-size: 22px;
        letter-spacing: -0.22px;
        margin-bottom: 16px;
    }
    .shopBlockData > div > p:not(:first-of-type) {
        font-size: 16px;
        line-height: 100%; /* 16px */
        letter-spacing: -0.32px;
    }
    .shopBlockData > div > p:nth-of-type(2) {
        margin-bottom: 16px;
    }
    .partners > div  {
        flex-direction: column;
        padding: 16px 0;
    }
    .partners > div > div:first-child {
        margin-bottom: 40px;
    }
    .partners > div > div:first-child > p:first-child {
        font-size: 22px;
    }
    .partners > div > div:first-child > p:not(:first-child),
    .partners > div > div:first-child > a {
        font-size: 16px;
    }
    .partners > div > div:last-child {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .partners > div > div:last-child > p:first-child  {
        font-size: 16px;
        margin-bottom: 0;
    }
}
